import React, { useEffect } from 'react';

class WidgetEmbedCode extends React.Component {
  componentWillUpdate() {
    if (document.getElementById('hs-script-loader')) {
      document.getElementById('hs-script-loader').remove();
    }
  }

  render() {
    const { portalId = 99209436, env = 'qa' } = this.props;

    return (
      <script
        type="text/javascript"
        id="hs-script-loader"
        async={true}
        defer={true}
        src={`//js.hs-scripts${env === 'qa' ? 'qa' : ''}.com/${portalId}.js`}
      />
    );
  }
}

export default WidgetEmbedCode;
